import { navList } from '~/src/components/NavSidebar/constants';
import { getCookie } from '~/src/method/screen';

// 攤平sidebar的選單, 取得有路由&權限的配對
const oneLevelRoute = (sidebarlist = [], newList = [], child = false) => {
  if (Array.isArray(sidebarlist)) {
    sidebarlist.forEach(e => {
      if (e.route && e.key) { newList.push(e); }
      if (Array.isArray(e.children)) {
        oneLevelRoute(e.children, newList, true);
      }
    });
  }
  // 最外層的迴圈跑完表示recursive已完成
	if(!child) {
    return newList;
	}
}

// 從redux or cookie取得該route所有呈現的按鈕
export const getAuthCtrl = (routePath, reduxUser) => {
  const authItem = oneLevelRoute(navList)
    .find(item => item.route.toLowerCase() === routePath.toLowerCase());
  
  const authKey = authItem && authItem.key ? authItem.key : null;
  // console.log(authItem, authKey);
  
  const strUserAuthList = reduxUser || getCookie('user');

  if (strUserAuthList) {
    const authPageList = JSON.parse(strUserAuthList).content;
    let authCtrl;
    // console.log(authPageList);
    for (let collapse in authPageList) {
      // console.log(authPageList[collapse][authKey], authKey);
      if (authPageList[collapse][authKey]) {
        authCtrl = authPageList[collapse][authKey]
      }
    }
    // console.log(authCtrl);
    return authCtrl;
  }
  return "";
}


