import { FETCH_COMPANY_DATA } from './constants';

const reactTableServerSide = (state = [], action) => {
  switch(action.type) {
    case FETCH_COMPANY_DATA:
      return [
        ...state,
        {
          requestURL: action.requestURL,
          status: action.status,
          items: action.items
        }
      ];
    default:
      return state;
  }
}

export default reactTableServerSide;


