import { FETCH_USER_DATA } from './constants';

const userManagement = (state = [], action) => {
  switch(action.type) {
    case FETCH_USER_DATA:
      return [
        // ...state,
        {
          ...action.payload
        }
      ];
    default:
      return state;
  }
}

export default userManagement;


