import { USER_LOG_OUT } from '~/src/reducers/Login/constants';

export const logout = () => {
  return (dispatch) => {
    return new Promise((resolve, reject) => {
      dispatch({
        type: USER_LOG_OUT
      });
      resolve('');
    });
  }
}


