import { USER_LOG_IN, USER_LOG_OUT } from './constants';

const login = (state = {}, action) => {
  switch(action.type) {
    case USER_LOG_IN:
      return {
        ...state,
        user: {...action.payload}
      }
		case USER_LOG_OUT:
			delete state.user;
      return {
        ...state
      }
    default:
      return state;
  }
}

export default login;


