import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import classnames from 'classnames';
import * as actions from './actions';
import { setCookie } from '~/src/method/screen';

class NavAccount extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navAccountItemsShow: false,
      selectedUserComplany: ''
    }
    this.logout = this.logout.bind(this);
    this.toggleTool = this.toggleTool.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);
  }

  componentDidMount() {
    document.addEventListener('click', this.handleClickOutside);
    this.setUserCompany();
  }
  componentWillUnmount() {
    document.removeEventListener('click', this.handleClickOutside);
  }

  setUserCompany() {
    const userCompanyId = localStorage.userCompanyId || '';
    if (userCompanyId) {
      this.setState({ selectedUserComplany: userCompanyId });
    }
  }

  handleClickOutside(event) {
    // console.log(event, this.wrapperRef);
    if ((this.wrapperRef && !this.wrapperRef.contains(event.target)) &&
      (this.toggleRef && !this.toggleRef.contains(event.target)) ) {
      this.setState({ navAccountItemsShow: false });
    }
  }

  logout() {
    const { actions } = this.props;
    actions
      .logout()
      .then(res => {
        // localStorage.user = "";
        setCookie('user', '', 0);
        // this.props.isLogout(true);
        setTimeout(() => {
          window.location.reload();
        }, 0);
        
      });
  }

  toggleTool(e) {
    this.setState({ navAccountItemsShow: !this.state.navAccountItemsShow });
  }

  render() {
    return (
      <div className="nav__account col-auto col-sm-7 col-md-7">
        {/*eslint-disable no-script-url*/}
        <a ref={c => { this.toggleRef = c }} className="nav__account__tool-toggle col-auto" href="javascript: void(0);" onClick={this.toggleTool}>
          <i className="ion-android-options"></i>
        </a>
        <div ref={c => { this.wrapperRef = c }} className={classnames({
          "nav__account__items": true,
          "nav__account__items--hide": !this.state.navAccountItemsShow,
          "nav__account__items--show": this.state.navAccountItemsShow,
        })}>
          <div className="col-lg-5 nav__account__item" style={{ order: 2 }}>
            <select className="form-control"
              value={this.state.selectedUserComplany}
              onChange={e => {
                this.setState({ selectedUserComplany: e.target.value });
                localStorage.setItem('userCompanyId', e.target.value);
              }}>
              {this.props.reduxLogin.user.items.data.suppliers.data.map((supplier, idx) => {
                // let key, value;
                // for (let s in supplier) {
                //   key = s;
                //   value = supplier[s];
                // }
                // if (key !== 'N/A') {
                //   return <option key={key} value={key}>{value}</option>
                // } else {
                //   return '';
                // }
                return <option key={supplier.id} value={supplier.id}>{supplier.supplier_company}</option>
              })}
            </select>
          </div>
          <div className="col-lg-auto nav__account__item" style={{ order: 1 }}>
            Hi, { this.props.reduxLogin.user.items.data.username }
          </div>
          <a className="col-lg-auto nav__account__item" href="javascript: void(0);" onClick={this.logout} style={{ order: 3 }}>
            <i className="fa fa-power-off"></i>&nbsp;Logout
          </a>
        </div>  
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  ...state
});

const mapDispatchProps = (dispatch, ownProps) => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchProps
)(NavAccount);


