import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as actions from './actions';
import NavItem from './NavItem';
import { navList } from './constants';

class NavSidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navList: []
    };
    this.toggle = this.toggle.bind(this);
  }
  componentDidMount() {
    this.setState({navList: this.showIfAllow()});
  }
  showIfAllow() {
    const userSidebar = this.props.reduxLogin.user.items.content;
    // console.log(userSidebar);
    const allowSidebar = navList.filter(item => userSidebar[item.key] !== undefined);
    
    return allowSidebar.map(item => {
      item.children = item.children.filter(i => {
        // console.log('console: ', userSidebar[item.key][i.key], item.key, i.key);
        return userSidebar[item.key][i.key] !== undefined;
      })
      return item;
    });
  }
  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  render() {
    return (
      <div className="sidebar__list">
        <ul>
          {this.state.navList.map((navItem, idx) =>
            <NavItem {...this.props} navItem={navItem} key={idx}/>
          )}
        </ul>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  ...state
});

const mapDispatchProps = (dispatch, ownProps) => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchProps
)(NavSidebar);


