const statusTrigger = (state = [], action) => {
  switch(action.type) {
    case 'TRIGGER_401':
      return [
        ...state,
        {
          requestURL: action.requestURL,
          status: action.status,
          items: action.items
        }
      ];
    default:
      return state;
  }
}

export default statusTrigger;


