import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import classnames from 'classnames';
import NavAccount from '~/src/components/NavAccount';
import NavSidebar from '~/src/components/NavSidebar';
import { documentSize } from '~/src/method/screen';
import { getAuthCtrl } from '~/src/method/auth';
import { breakpoints } from '~/src/constants/screen';
import logo from '~/src/assets/images/logo/Shopee-700x217.svg';

export default class Layout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: true,
      authCtrl: []
    };
    this.toggleSideBar = this.toggleSideBar.bind(this);
    this.sidebarCloseWhenSmallerSize = this.sidebarCloseWhenSmallerSize.bind(this);
    this.windowResize = this.windowResize.bind(this);
    this.setAuthCtrl = this.setAuthCtrl.bind(this);
  }
  componentDidMount() {
    window.addEventListener("resize", this.windowResize);
    this.sidebarCloseWhenSmallerSize();
    this.setAuthCtrl();
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.windowResize);
  }
  componentDidUpdate(prevProps) {
    const prevPath = prevProps.matchProps.match.path;
    const currentPath = this.props.matchProps.match.path;
    if ( prevPath !== currentPath ) {
      // console.log(prevPath, currentPath);
      this.sidebarCloseWhenSmallerSize();
      this.setAuthCtrl();
    }
  }
  windowResize(event) {
    this.sidebarCloseWhenSmallerSize();
  }
  // It will be called when the different route or window resizing
  sidebarCloseWhenSmallerSize() {
    // timeout make infrequently call
    this.resizeTimeout = setTimeout(() => {
      clearTimeout(this.resizeTimeout);

      const docSize = documentSize();
      const openSidebar = (docSize.width < breakpoints.lg) ? false: true;
      // console.log(docSize.width + ' × ' + docSize.height);
  
      this.setState({ sidebarOpen: openSidebar });
    }, 100);
  }
  toggleSideBar() {
    this.setState({ sidebarOpen: !this.state.sidebarOpen });
  }
  // 取得該路由被授予的物件權限
  setAuthCtrl() {
    const currentPath = this.props.matchProps.match.path;
    const authCtrl = getAuthCtrl(currentPath);
    this.setState({ authCtrl: authCtrl});
  }
  render() {
    const { matchProps, Component } = this.props;
    const { sidebarOpen } = this.state;
    // console.log(this.props.matchProps.history.listen);
    return (
      <div className="layout layout--default">
        <nav className="nav navbar-light nav--header">
          <Link className="navbar-brand" to="/">
            <img src={logo} height="45" className="d-inline-block align-top" alt="" />
          </Link>
          <button className="navbar-toggler" onClick={this.toggleSideBar}>
            <i className="ion-navicon-round" id=""></i>
          </button>
          <NavAccount {...matchProps} />
        </nav>
        <div className={classnames({
          'container-fluid': true,
          'layout__main': true,
          'sidebar--open': sidebarOpen,
          'sidebar--close': !sidebarOpen,
        })}>
          <div className="row">
            <aside className="sidebar sidebar--nav">
              <NavSidebar {...matchProps} />
            </aside>
            <main className="main">
              <div className="wrap-box--m">  
                <Component {...matchProps} authCtrl={this.state.authCtrl} />
              </div>  
            </main>
          </div>
        </div>
      </div>
    );
  }
}


