import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { Collapse } from 'reactstrap';
import classnames from 'classnames';

export default class NavSidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapse: false
    };
    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  matchRoute(match, mappingChildren) {
    let isMatchRoute = false;
    mappingChildren.map(item => { 
      if ([match.path.toLowerCase(), match.url.toLowerCase()].indexOf(item.route.toLowerCase()) > -1) {
        isMatchRoute = true;
      }
    });
    return isMatchRoute;
  }
  componentWillMount() {
    const { navItem, match } = this.props;
    const parentCollapsed = this.matchRoute(match, navItem.children);
    if (parentCollapsed) {
      this.setState({ collapse: parentCollapsed });
    }
  }
  componentDidMount() {
  }

  render() {
    const { navItem, match } = this.props;
    
    return (
      <li className={classnames({
          "sidebar__item--collapse": !this.state.collapse,
          "sidebar__item--collapsed": this.state.collapse
      })}>
        {/*eslint-disable no-script-url*/}
        <a href="javascript: void(0);" onClick={this.toggle}>
          <i className={navItem.iconClass}></i>{ navItem.label }
        </a>

        <Collapse isOpen={this.state.collapse}>
          <ul>
            {navItem.children.map((items, idx) =>
              <li key={idx} className={classnames({
                "sidebar__item__li--active": this.matchRoute(match, [items])
              })}>
                <NavLink
                  exact={items.exact}
                  activeClassName="sidebar__item--active"
                  to={items.route}>
                  {items.label}
                </NavLink>
              </li>
            )}
          </ul>
        </Collapse>
      </li>
    );
  }
}


