import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { ConnectedRouter } from 'react-router-redux';
import { Provider } from 'react-redux';
import createHistory from 'history/createBrowserHistory';
import Loadable from 'react-loadable';

import configureStore from '~/src/store/configureStore';
import DefaultLayout from '~/src/layouts/PageLayout';

import '~/src/assets/fonts/ionicons/ionicons.svg';
import '~/src/assets/fonts/ionicons/ionicons.eot';
import '~/src/assets/fonts/ionicons/ionicons.ttf';
import '~/src/assets/fonts/ionicons/ionicons.woff';
import '~/src/assets/fonts/fontawesome/fontawesome-webfont.svg';
import '~/src/assets/fonts/fontawesome/fontawesome-webfont.eot';
import '~/src/assets/fonts/fontawesome/fontawesome-webfont.ttf';
import '~/src/assets/fonts/fontawesome/fontawesome-webfont.woff';

const history = createHistory();

const Loading = (props) => {
  if(props.error) {
    return <div>Error!!!</div>
  }
  else if(props.pastDelay) {
    return <div>Loading...</div>
  }
  else {
    return <div>unknown.</div>
  }
} ;

const Home = Loadable({
  loader: () => import('../routes/Home' /* webpackChunkName: "home" */),
  loading: Loading
});

const About = Loadable({
  loader: () => import('../routes/About' /* webpackChunkName: "about" */),
  loading: Loading
});

const Sample = Loadable({
  loader: () => import('../routes/Sample' /* webpackChunkName: "sample" */),
  loading: Loading
});

const Login = Loadable({
  loader: () => import('../routes/Login' /* webpackChunkName: "login" */),
  loading: Loading
});

const UserManagement = Loadable({
  loader: () => import('../routes/UserManagement' /* webpackChunkName: "userManagement" */),
  loading: Loading
});

const store = configureStore();

const App = () => (
  <Provider store={store}>
    <ConnectedRouter history={history}>
      <Switch>
        <DefaultLayout exact path="/" component={Home}/>
        <DefaultLayout path="/about" component={About}/>
        <DefaultLayout exact path="/sample" component={Sample}/>
        <DefaultLayout path="/sample/a" component={Home} />
        <DefaultLayout path="/userManagement" component={UserManagement} />
        <Route path="/login" component={Login} />
      </Switch>
    </ConnectedRouter>
  </Provider>
);

export default App;


